import React, { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';

import { CSSTransition } from 'react-transition-group';

import Loader from './Loader';
import MovieItem from './MovieItem';

import NoPoster from '../assets/poster-na.png';

const SearchMovie = ({ sRef }) => {
  const { title } = useParams();
  const [show, setShow] = useState(false);
  const [movieList, setMovieList] = useState(null);

  useEffect(() => {
    getMovieList(title).then((list) => {
      sRef.current.classList.add('is-searching');
      setMovieList(list);
      setShow(true);
    });

    return () => {
      setShow(false);
    };
  }, [title]);

  const getMovieList = async (movieTitle) => {
    const API_KEY = process.env.REACT_APP_API_KEY;

    const response = await fetch(`http://www.omdbapi.com/?apikey=${API_KEY}&s=${movieTitle}`);
    const data = await response.json();

    console.log(data.Search);
    if (data.Search) return removeDuplicates(data.Search);

    return undefined;
  };

  if (movieList === null) {
    return <Loader />;
  }
  if (movieList === undefined) {
    return (
      <CSSTransition in={show} timeout={500} classNames="movie-list-item" unmountOnExit>
        {/* <MovieItem key={`movie-${item.imdbID}`} title={item.Title} id={item.imdbID} year={item.Year} image={poster} style={tDelay} /> */}
        <h3 className="no-results">
          Nessun risultato per:
          <em>{` ${title}`}</em>
        </h3>
      </CSSTransition>
    );
  }
  return (
    // <TransitionGroup className="movie-list-item-group">
    <div className="movie-list">
      {movieList.map((item, i) => {
        const delay = 300 + i * 75;
        const tDelay = { transitionDelay: `${delay}ms` };
        const poster = item.Poster !== 'N/A' ? item.Poster : NoPoster;

        return (
          <CSSTransition in={show} key={`movie-${item.imdbID}`} timeout={500 + delay} classNames="movie-list-item" unmountOnExit>
            <MovieItem key={`movie-${item.imdbID}`} title={item.Title} id={item.imdbID} year={item.Year} image={poster} style={tDelay} />
          </CSSTransition>
        );
      })}
    </div>
    // </TransitionGroup>
  );
};

function removeDuplicates(arr) {
  const unique = arr
    .map((e) => e.imdbID)
    .map((e, i, final) => final.indexOf(e) === i && i)
    .filter((e) => arr[e])
    .map((e) => arr[e]);

  return unique;
}

export default SearchMovie;
