import React, { useState } from 'react';
// import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';

const MovieItem = ({ id, title, year, image, style }) => {
  const [loading, setLoading] = useState(false);

  if (loading) {
    return null;
  }

  return (
    <Link to={'/title/' + id} style={style}>
      <div className="movie-item">
        <img src={image} alt="" className="movie-item-image" onLoad={() => setLoading(false)} />
        <div className="movie-item-info">
          <p className="movie-item-title">{title}</p>
          <p className="movie-item-year secondary">{year}</p>
        </div>
        {/* <p className="movie-id">{id}</p> */}
      </div>
    </Link>
  );
};

// MovieItem.propTypes = {
//   id: PropTypes.string.isRequired,
//   title: PropTypes.string.isRequired,
//   year: PropTypes.string.isRequired,
//   image: PropTypes.string.isRequired,
// };

export default MovieItem;
