import React, { useState } from 'react';
import { Route, Switch, useHistory } from 'react-router-dom';
import { withRouter } from 'react-router';

import MovieDetails from './components/MovieDetails';
import SearchMovie from './components/SearchMovie';
import ThemeSwitcher from './components/ThemeSwitcher';

const Main = () => {
  const [movieSearch, setMovieSearch] = useState('');
  const history = useHistory();

  const searchRef = React.createRef();

  const updateSearch = (e) => {
    setMovieSearch(e.target.value);
  };

  const searchMovie = (e) => {
    e.preventDefault();
    if (movieSearch) {
      history.push('/search/' + movieSearch);
    }
  };

  return (
    <div className="App">
      <div className="header" ref={searchRef}>
        {/* <ThemeSwitcher /> */}
        <h1 id="title">
          <a href="/">iDMR</a>
        </h1>

        <h3 id="subtitle">internet Danilo Movie Ratings</h3>
        <form className="search-form" onSubmit={searchMovie}>
          <div className="search-wrap">
            <input
              type="text"
              className="input-search"
              placeholder="Cerca film o serie tv..."
              onChange={updateSearch}
              value={movieSearch}
            />
            <button type="submit" className="search-button">
              <i className="fa fa-search"></i>
            </button>
          </div>
        </form>
      </div>
      <Switch>
        <Route exact path="/" />
        <section id="main">
          <Route path="/search/:title">
            <SearchMovie sRef={searchRef} />
          </Route>
          <Route path="/title/:id">
            <MovieDetails sRef={searchRef} />
          </Route>
        </section>
      </Switch>
    </div>
  );
};

export default withRouter(Main);
