import React, { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';

import api from '../api/api';
import Loader from './Loader';

import NoPoster from '../assets/poster-na.png';

import iImdb from '../assets/imdb.svg';
import iRtFresh from '../assets/rt-fresh.png';
import iRtRotten from '../assets/rt-rotten.png';
import iRtCertified from '../assets/rt-certified.png';

const MovieDetails = ({ sRef }) => {
  const { id } = useParams();

  const [movie, setMovie] = useState({});
  const [opinion, setOpinion] = useState('');

  const getMovie = async (imdbId) => {
    const API_KEY = process.env.REACT_APP_API_KEY;

    const response = await fetch(`http://www.omdbapi.com/?apikey=${API_KEY}&i=${imdbId}`);
    const data = await response.json();

    console.log(data);
    if (data.Response === 'False') {
      return { error: true };
    }
    return data;
  };

  useEffect(() => {
    getMovie(id).then((res) => {
      sRef.current.classList.add('is-searching');
      setMovie(res);
    });
  }, [id]);

  if (movie.error) {
    return <h2>Film non trovato.</h2>;
  }
  if (Object.keys(movie).length === 0) {
    return <Loader />;
  }

  if (!opinion) {
    const tmpOpinion = api.directors.includes(movie.Director)
      ? api.good[Math.floor(Math.random() * api.good.length)]
      : api.bad[Math.floor(Math.random() * api.bad.length)];

    setOpinion(tmpOpinion.replace('$1', movie.Director));
  }

  const movieRuntime = Math.floor(movie.Runtime.split(' ', 1) / 60) + 'h ' + Math.floor(movie.Runtime.split(' ', 1) % 60) + 'min';
  const poster = movie.Poster !== 'N/A' ? movie.Poster : NoPoster;

  return (
    <div className="movie-card">
      <img className="movie-card-image" src={poster} alt="Movie Poster" />
      <div className="movie-card-content">
        <p className="movie-card-title">{movie.Title}</p>
        <p className="movie-card-director secondary">{movie.Director}</p>
        <div className="movie-card-infos secondary">
          <p className="movie-card-infos-rated">{movie.Rated}</p>
          <p className="movie-card-infos-runtime">{movieRuntime}</p>
          <p className="movie-card-infos-year">{movie.Year}</p>
        </div>
        <div className="movie-card-ratings">
          {movie.Ratings.map((rating) => (
            <Rating key={`rating-${rating.Source}`} rating={rating} />
          ))}
        </div>
        <blockquote>
          <p
            className="movie-card-infos-quote"
            dangerouslySetInnerHTML={{
              __html: opinion,
            }}
          />
          <footer>Danilo Solimeno</footer>
        </blockquote>
      </div>
    </div>
  );
};

const Rating = ({ rating }) => {
  let source;
  let value = rating.Value;
  const tmpNum = parseFloat(value.match(/[\d.]+/), 10);

  switch (rating.Source) {
    case 'Internet Movie Database':
      source = iImdb;
      value = tmpNum;
      break;
    case 'Rotten Tomatoes':
      if (tmpNum > 75) {
        source = iRtCertified;
      } else if (tmpNum < 60) {
        source = iRtRotten;
      } else {
        source = iRtFresh;
      }
      break;
    case 'Metacritic':
      if (tmpNum >= 75) {
        value = <span className="mc-good">{tmpNum}</span>;
      } else if (tmpNum < 40) {
        value = <span className="mc-bad">{tmpNum}</span>;
      } else {
        value = <span className="mc-mixed">{tmpNum}</span>;
      }
      break;
    default:
      console.log('error');
  }
  return (
    <div className="movie-card-ratings-item">
      {source && <img src={source} alt={`${rating.Source} rating`} height="18px" />}
      <span>{value}</span>
    </div>
  );
};

export default MovieDetails;
